<template lang="pug">

.product(:class="{ active }")
	.information(@click="showInformation")
		v-icon-information
	.img
		v-icon-avocado
	.name {{ product.name }}
	.price {{ product.price }} руб/шт
	.toggle(@click="toggleProduct" :class="{ remove: this.product.cart }")
		v-icon-plus

</template>
<script>
export default {
	data: () => ({
		active: false
	}),
	methods: {
		showInformation() {
			this.$store.dispatch('ui/setPopup', { name: 'product', query: this.product.id })
		},
		toggleProduct() {
			if (this.product.cart)
				this.$store.commit('catalog/removeProductFromCart', this.product)
			else
				this.$store.commit('catalog/setProductCount', { product: this.product, count: 1 })
		},
		focus(event) {
			this.active = true
		},
		blur(event) {
			this.active = false
		},
	},
	props: {
		product: {
			type: Object,
			required: true
		},
		isCart: {
			type: Boolean
		}
	},
}
</script>
<style lang="stylus">
.product
	.toggle
		svg
			path
				stroke #FBF5DB
</style>
<style lang="stylus" scoped>

.product
	background-color #FBF5DB
	border-radius 10px
	width 150px
	position relative
	padding 13px 10px 10px
	box-shadow: 0px 4px 5px rgba(204, 191, 171, 0.5)
	margin-bottom 20px
	margin-right 20px
	&:nth-child(3n+3)
		margin-right 0

.img
	text-align center
	svg
		width 33px

.name
	margin-top 5px
	font-size 12px
	text-align center
	color $green

.price
	margin-top 8px
	font-size 15px
	text-align center
	color $green
	height 25px
	border-radius 25px
	line-height 25px
	background-color #fff

.toggle, .information
	z-index 2
	position absolute
	width 20px
	height 20px
	border-radius 100%
	display flex
	align-items center
	justify-content center
	transition box-shadow .2s, background-color .15s, transform .15s
	cursor pointer

.information
	top -6px
	left -6px
	background-color #fff
	border: 1.5px solid #8E8E93
	padding-bottom 1px
	box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.6)
	svg
		width 3px
	&:hover
		box-shadow 0px 4px 6px rgba(204, 191, 171, 0.4)
	&:active
		box-shadow 0px 2px 3px rgba(204, 191, 171, 0.1)

.toggle
	top -5px
	right -5px
	background-color $green
	box-shadow 0px 4px 6px rgba(204, 191, 171, 0.25)
	&:hover
		box-shadow 0px 4px 6px rgba(204, 191, 171, 0.1)
	&:active
		box-shadow 0px 2px 3px rgba(204, 191, 171, 0.1)
	svg
		width 16px
	&.remove
		transform rotate(45deg)
		background-color $pink-red

</style>