<template lang="pug">

.cart
	//- v-navbar
	.title Корзина
		v-icon-cart
	.products
		.inner
			v-product(v-for="product in cart" :key="product.id" :product="product" :isCart="true")
	transition(name="show")
		.buy(v-if="cart.length > 0")
			span Оформить заказ

</template>
<script>
import vProduct from './product'
import vNavbar from './navbar'
export default {
	data: () => ({
		height: 0
	}),
	components: {
		vNavbar,
		vProduct
	},
	computed: {
		cart() {
			return this.$store.getters['catalog/cart']
		},
		cartLength() {
			return this.cart.length
		}
	},
	watch: {
		cartLength: {
			immediate: true,
			async handler(length) {
				await this.$nextTick()
				this.height = this.$el.querySelector('.inner').offsetHeight
			}
		}
	},
}
</script>
<style lang="stylus">
.title
	svg
		path
			fill #FEA472
</style>
<style lang="stylus" scoped>

.cart
	width 50%
	width 330px
	flex-shrink 0
	position relative

.title
	font-size 20px
	margin-bottom 20px
	color #FEA472
	font-weight 500
	font-size 22px
	display flex
	align-items center
	justify-content flex-end
	svg
		width 31px
		margin-left 20px

.products
	transition height .3s
	.inner
		transition height .3s
		display flex
		flex-direction row
		flex-wrap wrap
		align-items flex-start

.buy
	text-align center
	&.show-enter-active
		animation show .3s
	&.show-leave-active
		animation show .3s reverse
	span
		font-size 16px
		font-weight 400
		display inline-block
		padding 0 20px
		height 35px
		border-radius 35px
		line-height 35px
		color #fff
		background-color $green
		box-shadow 0 2px 3px rgba(#000, .2)
		cursor pointer
		transition box-shadow .2s
		&:hover
			box-shadow 0 2px 3px rgba(#000, .1)
		&:active
			box-shadow 0 2px 3px rgba(#000, .05)
		
@keyframes show
	0%
		transform scale(0)
	100%
		transform scale(1)

</style>