<template lang="pug">

transition(name="show" appear)
	.product(:class="{ active }")
		.information(@click="showInformation")
			v-icon-information
		.img
			v-icon-avocado
		.name {{ product.name }}
		.price {{ (product.price * product.count).toFixed(2) }} руб
		v-calculator(:product="product" @focus="focus" @blur="blur")
		.remove(v-if="isCart" @click="removeProduct")
			v-icon-exit

</template>
<script>
import vCalculator from './calculator'
export default {
	mounted() {
		// console.log(this.$el.offsetHeight)
	},
	data: () => ({
		active: false
	}),
	methods: {
		showInformation() {
			this.$store.dispatch('ui/setPopup', { name: 'product', query: this.product.id })
		},
		removeProduct() {
			this.$store.commit('catalog/removeProductFromCart', this.product)
		},
		focus(event) {
			this.active = true
		},
		blur(event) {
			this.active = false
		}
	},
	props: {
		product: {
			type: Object,
			required: true
		},
		isCart: {
			type: Boolean
		}
	},
	components: {
		vCalculator
	},
}
</script>
<style lang="stylus" scoped>

.product
	background-color #FBFBFB
	border-radius 10px
	width 150px
	position relative
	padding 13px 10px 10px
	box-shadow: 0px 4px 5px rgba(204, 191, 171, 0.5)
	margin-right 30px
	margin-bottom 50px
	&:nth-child(2n+2)
		margin-right 0
	&.show-enter-active
		animation show .3s
	&.show-leave-active
		animation show .3s reverse

@keyframes show
	0%
		transform scale(0)
	100%
		transform scale(1)

.img
	text-align center
	svg
		width 33px

.name
	margin-top 5px
	font-size 12px
	text-align center
	color #61B335

.price
	margin-top 5px
	font-size 15px
	text-align center
	color #61B335
	height 25px
	border-radius 25px
	line-height 25px
	background-color #EBEDEB

.remove, .information
	width 25px
	height 25px
	display flex
	align-items center
	justify-content center
	border-radius 100%
	cursor pointer
	user-select none
	transition box-shadow .2s
	position absolute

.remove
	top -8px
	right -8px
	padding-top 1px
	box-shadow: 0px 4px 6px rgba(204, 191, 171, 0.25)
	background-color $pink-red
	svg
		width 13px
	&:hover
		box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.25)
	&:active
		box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.1)

.information
	top -10px
	left -10px
	background-color #fff
	box-shadow: 0px 4px 4px rgba(204, 191, 171, 0.6)
	border: 1.5px solid #8E8E93
	padding-bottom 1px
	padding-right 1px
	svg
		width 3.5px
	&:hover
		box-shadow 0px 4px 6px rgba(204, 191, 171, 0.4)
	&:active
		box-shadow 0px 2px 3px rgba(204, 191, 171, 0.1)

</style>