<template lang="pug">

.navbar
	.search
		input(v-model="search" placeholder="Найти")
		v-search-icon
	//- .sort
	//- 	.sort-item(v-for="item in sort" :class="{ active: active.name == item.name, up: active.direction != item.direction }" @click="active = item") {{ item.title }}

</template>
<script>
import vSearchIcon from 'icons/search'
export default {
	data: ({ $store }) => ({
		search: $store.state.catalog.server.search,
		sort: [
			{
				title: 'Популярность',
				name: 'popularity',
				direction: -1
			},
			{
				title: 'Алфавит',
				name: 'name',
				direction: 1
			},
			{
				title: 'Цена',
				name: 'price',
				direction: 1
			},
		],
	}),
	computed: {
		active: {
			get() {
				return this.$store.state.catalog.server.sort
			},
			set(item) {
				const direction = this.active.name == item.name ? this.active.direction * -1 : item.direction

				this.$store.dispatch('catalog/setFilter', {
					name: 'setSort',
					data: { name: item.name, direction }
				})
			}
		},
	},
	watch: {
		search(text) {
			this.$store.dispatch('catalog/setLoading', true)
			setTimeout(() => {
				if (text == this.search)
					this.$store.dispatch('catalog/setFilter', {
						name: 'setSearch',
						data: text
					})
			}, 1000)
		}
	},
	components: {
		vSearchIcon
	}
}
</script>
<style lang="stylus" scoped>

.navbar
	display flex
	align-items center
	justify-content space-between
	margin-bottom 20px

.search
	position relative
	height 32px
	width 295px
	background-color #F8F9F8
	border-radius 10px
	input
		background-color transparent
		border 0
		width 100%
		height 100%
		padding-left 30px
		font-size 15px
		&::placeholder
			color #8E8E93
	svg
		top 8px
		left 8px
		width 16px
		position absolute

.sort
	display flex
	&-item
		font-size 16px
		padding-left 25px
		cursor pointer
		color #aaaaaa
		user-select none
		position relative
		&.active
			color #000000
			&:after
				content ''
				position absolute
				right -15px
				top 6px
				width 10px
				height 10px
				background-image url('~images/ui/arrow-down.svg')
				background-size contain
				background-repeat no-repeat
				background-position center
				transition transform .2s
				filter invert(100%)
			&.up
				&:after
					transform rotate(180deg)

</style>