<template lang="pug">

.category(@click="toggleCategory" :class="{ active }") {{ category.name }}

</template>
<script>
export default {
	methods: {
		toggleCategory(){
			this.$emit('toggleCategory')
		}
	},
	props: {
		category: {
			type: Object
		},
		active: {
			type: Boolean
		}
	},
}
</script>
<style lang="stylus" scoped>

.category
	font-size 15px
	color #8E8E93
	background: #F8F9F8
	box-shadow: 0px 3px 6px rgba(204, 191, 171, 0.6)
	border-radius: 8px
	height 90px
	width 200px
	margin-right 20px
	display flex
	align-items center
	justify-content center
	margin-bottom 20px
	cursor pointer
	transition color .1s, box-shadow .2s
	user-select none
	&:hover
		box-shadow: 0px 2px 5px rgba(204, 191, 171, 0.6)
	&:active
		box-shadow: 0px 2px 3px rgba(204, 191, 171, 0.3)
	&.active
		color $green

</style>